.butterfly {
  position: absolute;
  width: 85px;
  top: 135px;
  left: 240px;
  animation: flutter 3s ease-in-out infinite;
  filter: drop-shadow(0px 6px 14px rgba(0,0,0,0.25));
  z-index: 10;
}
@keyframes flutter {
  0% {
    transform: translateY(0px) scaleX(-1);
  }
  50% {
    transform: translateY(-6px) scaleX(-1);
  }
  100% {
    transform: translateY(0px) scaleX(-1);
  }
}
